import { useState } from 'react';
import { PillButton } from '~/common/ui-elements';

const COUNTDOWN = 3;

export const HaloCountdown = () => {
  const [currentTime, setCurrentTime] = useState(COUNTDOWN);
  const [showCountdown, setShowCountdown] = useState(false);

  const beginCountdown = () => {
    setShowCountdown(true);

    // Generate an array of the length of the number of currentTime to loop that many times
    Array.from(Array(currentTime).keys())
      .reverse()
      .forEach(() => {
        setTimeout(() => {
          setCurrentTime((prevTime) => prevTime - 1);
        }, 1000);
      });

    setShowCountdown(false);
    setCurrentTime(COUNTDOWN);
  };

  console.log('CURRENT TIME', currentTime);

  return (
    <div>
      <PillButton
        className="bg-theme-blue"
        disabled={showCountdown}
        onClick={() => {
          beginCountdown();
        }}
      >
        Start
      </PillButton>
    </div>
  );
};
